<template>
	<v-card class="ma-md-6 ma-sm-3">
		<v-card-title class="py-2">
			<v-icon class="mr-3">mdi-face</v-icon>
			Daftar Peserta
		</v-card-title>
		<v-divider></v-divider>
		<v-card-text>
			<v-form ref="form" v-model="valid">
        <v-row dense>
          <v-col cols="12" md="4" sm="6" class="py-0">
            <v-autocomplete
              label="Provinsi"
              outlined
              dense
              v-model="dataFilter.provinsi"
              :items="dataProv"
              item-text="nama"
              item-value="id"
              required
              :rules="requiredRule"
            ></v-autocomplete>
          </v-col>
          <v-col cols="12" md="4" sm="6" class="py-0">
            <v-autocomplete
              label="Kabupaten"
              outlined
              dense
              v-model="dataFilter.kabupaten"
              :items="dataKab"
              item-text="nama"
              item-value="id"
              :disabled="dataFilter.provinsi===''?true:false"
              required
              :rules="requiredRule"
            ></v-autocomplete>
          </v-col>
          <v-col cols="12" md="4" sm="6" class="py-0">
            <v-autocomplete
              label="Jenjang"
              outlined
              dense
              :disabled="dataFilter.kabupaten===''?true:false"
              v-model="dataFilter.jenjang"
              :items="dataJenjang"
              item-text="nama"
              item-value="id"
              required
              :rules="requiredRule"
            ></v-autocomplete>
          </v-col>
          <v-col cols="12" md="4" sm="6" class="py-0">
            <v-autocomplete
              label="Sekolah"
              outlined
              dense
              :disabled="dataFilter.jenjang===''?true:false"
              v-model="dataFilter.sekolah"
              :items="filterSekolah"
              item-text="sekolah"
              item-value="id"
              required
              :rules="requiredRule"
            ></v-autocomplete>
          </v-col>
          <v-col cols="12" md="4" sm="6" class="py-0">
            <v-autocomplete
              label="Kelas"
              outlined
              dense
              v-model="dataFilter.kelas"
              :items="dataKelas"
              item-text="nama"
              item-value="id"
              required
              :rules="requiredRule"
            ></v-autocomplete>
          </v-col>
          <v-col cols="12" md="4" sm="6" class="py-0">
            <v-autocomplete
              v-if="ptn === ''"
              label="Universitas"
              :items="dataPtnByNama"
              item-text="nama"
              item-value="nama"
              v-model="ptn"
              outlined
              dense
              required
              :rules="requiredRule"
            ></v-autocomplete>
            <v-autocomplete
              v-else
              label="Program Studi"
              :items="dataProdi"
              item-text="prodi.nama"
              item-value="id"
              v-model="dataFilter.prodi"
              outlined
              dense
              required
              append-outer-icon="mdi-close"
              @click:append-outer="clearPtn"
              :rules="requiredRule"
            ></v-autocomplete>
          </v-col>
        </v-row>
        <v-btn
          type="submit"
          :disabled="!valid"
          :loading="isLoading"
          @click.prevent="validate"
          block color="primary"
          depressed>
          filter
        </v-btn>
      </v-form>

			<DataPeserta/>
		</v-card-text>
	</v-card>
</template>

<script>
	import { mapState } from 'vuex'
	import DataPeserta from '@/components/Peserta/DataPeserta'

	export default{
		data: () => ({
			valid: true,
			requiredRule: [v => !!v || 'Data harus diisi'],			
			dataFilter: {
				provinsi: '',
				kabupaten: '',
				jenjang: '',
				sekolah: '',
				kelas: '',
				prodi: ''
			},
			ptn: ''
		}),

		created() {
			this.$store.dispatch('dataTempat/getDataProv')			
			this.$store.dispatch('dataJenjang/getDataJenjang')
			this.$store.dispatch('dataKelas/getDataKelas')
      this.$store.dispatch('dataPtn/getDataPtnByName')
		},

		computed: {
			...mapState('dataTempat', ['dataProv', 'dataKab']),
			...mapState('dataJenjang', ['dataJenjang']),
			...mapState('dataKelas', ['dataKelas']),
			...mapState('dataPtn', ['dataPtnByNama', 'dataProdi']),
			...mapState('dataSekolah', ['dataSekolah']),
			...mapState('dataPeserta', ['isLoading']),
			filterSekolah() {
				return this.dataSekolah.filter(x => x.jenjang.id === this.dataFilter.jenjang)
			}
		},

		components: {
			DataPeserta
		},

		watch: {
			'dataFilter.provinsi': function (val) {
				this.$store.dispatch('dataTempat/getDataKab', val)
			},
			'dataFilter.jenjang': function (val) {
				this.$store.dispatch('dataSekolah/getDataSekolahFilter', {
					provinsi_id: this.dataFilter.provinsi,
					kabupaten_id: this.dataFilter.kabupaten
				})
			},
      ptn(val) {
        this.$store.dispatch('dataPtn/getDataProdi', val)
      }
		},

		methods: {
			clearPtn() {
        this.ptn = ''
      },
      validate () {
        if (this.$refs.form.validate()) {
          this.$store.dispatch('dataPeserta/getDataPesertaFilter', this.dataFilter)
          this.$store.dispatch('dataPeserta/updateIsLoading', true)
        }
      }
		}
	}
</script>